.about-home {
  position: relative;
  padding: 130px 0;
}

.about-home_bg {
  top: -500px;
  height: calc(100% + 350px);
}

.about-home_content {
  max-width: 570px;
  width: 100%;
  color: #fff;
}

.about-home_content h4 {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 15px;
  color: #fff;
}

.about-home_content .undertitle {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 25px;
}

.about-home_content .text {
  font-size: 17px;
  line-height: 26px;
  margin-bottom: 25px;
}

@media screen and (max-width: 990px) {
  .about-home_bg {
    top: 0;
    height: 100%;
  }
}

@media screen and (max-width: 500px) {
  .about-home {
    padding: 50px 0;
  }
  .about-home_content h4 {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 15px;
  }
  .about-home_content .undertitle {
    font-size: 21px;
    line-height: 28px;
    margin-bottom: 15px;
  }
  .about-home_content .text {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 15px;
  }
}

